.flex-col-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login {
  margin-top: 288px;
  composes: flex-col-center;
  gap: 24px;
}

.additionalActions {
  composes: flex-col-center;
  gap: 16px;
  margin-top: 80px;
}

.link {
  color: rgba(76, 76, 255, 1);
  margin-left: 10px;
  cursor: pointer;
  text-decoration: none;
  transition: all .3s ease-in;
}

.link:hover {
  opacity: .6;
}

.messageContaner {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  color: var(--colors-interface-success);
}

.errorMessage {
  color: var(--colors-interface-error);
}

.loader {
  margin-top: 20px;
  font-size: 10px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  animation: mulShdSpin 1.1s infinite ease;
  transform: translateZ(0);
}

.loaderOrderPending {  
  margin-bottom: 120px;
  font-size: 10px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  animation: mulShdSpin 1.1s infinite ease;
  transform: translateZ(0);
}

